<template>
  <div v-if="anotherUser">
    <mu-sub-header style="font-size: 18px;">用户主页：{{anotherUser.attributes.username}}</mu-sub-header>
    <mu-flexbox>
      <mu-flexbox-item class="flex-demo">
        <mu-avatar :size="84" backgroundColor="white" :src="anotherUser.attributes.avatar.attributes.url"/>
      </mu-flexbox-item>
    </mu-flexbox>
    <mu-flexbox class="">
      <mu-flexbox-item class="flex-demo">
        <h2>{{anotherUser.attributes.nickname}}</h2>
      </mu-flexbox-item>
    </mu-flexbox>
    <mu-flexbox class="">
      <mu-flexbox-item class="flex-demo">
        <h4>{{anotherUser.attributes.desc}}</h4>
      </mu-flexbox-item>
    </mu-flexbox>
    <mu-flexbox class="">
      <mu-flexbox-item class="flex-demo" v-if="buttonEnable">
        <mu-raised-button :label="isFollowed ? '取消关注':'关注'" primary @click="toggleFollowUser"
                          :icon=" isFollowed ? 'person':'person_add'"/>
        <mu-raised-button label="私 信" primary @click="dialog = true" icon="message" :disable="logTag"/>
      </mu-flexbox-item>
    </mu-flexbox>

    <mu-sub-header>ta的发帖</mu-sub-header>

    <mu-divider/>

    <mu-list-item v-for="item in list" :title="item.attributes.title" @click="goPostPage(item)" :describeText="item.attributes.creater">
              <span slot="describe">
        <span style="color: rgba(0, 0, 0, .87)">{{item.attributes.createrName}}</span>
      </span>
    </mu-list-item>


    <mu-dialog :open="dialog" :title="'发送私信给：'+anotherUser.attributes.username" @close="dialog =  false">


        <mu-text-field hintText="最多不超过25个字符" :errorText="inputErrorText" @textOverflow="handleInputOverflow" :maxLength="25"
                        style="width: 230px" v-model="message"/><br/>
      <mu-flat-button slot="actions" @click="dialog = false" label="取消"/>
      <mu-flat-button slot="actions" primary @click="sendMessage" label="确定" :disabled="sendEnable"/>
    </mu-dialog>
  </div>
</template>
</div>
</template>
<script>
  import {mapState} from 'vuex'
  import AV from 'leancloud-storage'
  export default {
    data () {
      return {
        open: false,
        dialog:false,
        inputErrorText: '',
        sendEnable: false,
        message: '',
        list: [],
      }
    },

    methods: {
      goPostPage(item) {
        this.$store.commit('setPostPage',item)
        this.$router.push({path: '/post/'+item.id})
      },
      handleInputOverflow (isOverflow) {
        this.inputErrorText = isOverflow ? '不能超过25个字符' : '';
        this.sendEnable = isOverflow;
      },
      sendMessage () {
          console.log(this.anotherUser);
        this.$store.dispatch('sendMessageTo',{userId:this.anotherUser.id,message: this.message});
        this.dialog = false;
        this.message= '';
        //this.$router.push({path: '/message/privatemessage/' + this.$route.params.id})
      },
      toggleFollowUser () {
        this.$store.dispatch('toggleFollowUser', this.$route.params.id)
      }
    },
    mounted () {
      this.trigger = this.$el
    },
    beforeCreate: function () {
      // 获取用户关注的用户列表
      this.$store.dispatch('fetchAnotherUser', this.$route.params.id)
      this.$store.dispatch('checkHasFollowed', this.$route.params.id)
    },
    created: function () {
      let that = this
      this.anotherUser.attributes.post.forEach( item => {
        let query = new AV.Query('Post');
        query.get(item.id).then(function (post) {
          // 成功获得实例
          console.log(post)
          that.list.push(post)
        }, function (error) {
          // 异常处理
        });
        this.refreshing = false
    })
    },
    computed: {
      ...mapState([
        'anotherUser', 'userFollowList', 'user', 'isFollowed','logTag'
      ]),
      buttonEnable () {
        if (this.user.id == this.anotherUser.id)
          return false
        else
          return true
      }
    },
  }
</script>

<style>
  .flex-demo {
    text-align: center;
  }

  .mt12 {
    margin-top: 12px;
  }
</style>



