<template>
  <div v-if="mailBox">
    <mu-list>
      <mu-sub-header></mu-sub-header>
      <mu-list-item v-for="item in mailBox" :title="item.from.attributes.nickname">
        <mu-avatar :src="item.from.attributes.avatar.attributes.url" slot="leftAvatar"/>
        <span slot="describe">
        <span>-</span> {{item.data.message}}
      </span>
        <mu-icon-menu slot="right" icon="more_vert" tooltip="操作">
          <mu-menu-item title="回复" @click="openDialog(item.from)"/>

        </mu-icon-menu>
      </mu-list-item>
      <mu-divider inset/>
    </mu-list>
    <mu-dialog :open="dialog" :title="'回复给：'+auser.attributes.nickname">
      <mu-text-field hintText="最多不超过25个字符" :errorText="inputErrorText" @textOverflow="handleInputOverflow" :maxLength="25"
                     style="width: 230px" v-model="message"/><br/>      <mu-flat-button label="取 消" slot="actions" @click="dialog = false"/>
      <mu-flat-button primary label="确 定" slot="actions"  @click="sendMessageTo" :disabled="sendEnable"/>
    </mu-dialog>

    <div class="mu-overlay" style="display: none"></div>
  </div>
</template>
</div>
</template>
<script>
  import {mapState} from 'vuex'
  export default {
    data () {
      return {
        dialog: false,
        message: null,
        auser: {attributes:{nickname:''}},
        inputErrorText: '',
        sendEnable: false
      }
    },
    methods: {
      handleInputOverflow (isOverflow) {
        this.inputErrorText = isOverflow ? '不能超过25个字符' : '';
        this.sendEnable = isOverflow;
      },
      openDialog (user) {
        //  console.log(user);
       this.dialog = true
        this.auser =  user;
      },
      sendMessageTo() {
          this.dialog = false
        this.$store.dispatch('sendMessageTo', {userId: this.auser.id, message: this.message})
        this.message = null
      }
    },
    created: function () {
      this.$store.dispatch('checkMailBox')
    },
    computed: {
      ...mapState([
        'mailBox', 'messageUsers'
      ]),
    }
  }
</script>

<style scoped>
  .mu-overlay{
    opacity: 0.4;
    background-color: rgb(0, 0, 0);
    position: fixed;
    z-index: 20141249;
  }
</style>



