<template>
  <div>
    <mu-sub-header style="font-size: 18px;">发表一个帖子</mu-sub-header>

    <mu-content-block>
      <mu-text-field v-model="post.title" label="标题" hintText="最多不超过10个字符" :errorText="titleErrorText"
                     @textOverflow="handleTitleOverflow" :maxLength="20" fullWidth/>
      <br/>
      <mu-text-field v-model="post.content" label="内容" multiLine :rows="5" :rowsMax="6" fullWidth/>
      <br/>

      <mu-sub-header>限制3张(剩余{{3-imgCount}}张),再次点击图片删除</mu-sub-header>

      <mu-list>
        <mu-list-item disableRipple v-for="(imgUrl,index) in imgList" style="float: left;" @click="deleteImg(index)">
            <mu-avatar slot="left" :src="imgUrl" />
        </mu-list-item>
      </mu-list>

      <mu-raised-button label="添加图片" primary>
        <input type="file" class="file-button" accept="image/jpeg, image/png,image/gif" v-on:change="handleImg" id="avatar">
      </mu-raised-button>

      <div class="mu-text-field-label">标签</div>
      <mu-chip class="chip" :backgroundColor="tagQA?'grey700':''" style="color: #fff;" @click="selectTagQA">
        <mu-avatar :backgroundColor="tagQA?'grey900':''" :size="32" icon="question_answer"/>
        问答
      </mu-chip>

      <mu-chip :backgroundColor="tagShare?'grey700':''" class="chip" style="color: #fff;" @click="selectTagShare">
        <mu-avatar :backgroundColor="tagShare?'grey900':''" :size="32" icon="share"/>
        分享
      </mu-chip>

      <mu-chip :backgroundColor="tagOther?'grey700':''" class="chip" style="color: #fff;" @click="selectTagOther">
        <mu-avatar :backgroundColor="tagOther?'grey900':''" :size="32" icon="more_horiz"/>
        其他
      </mu-chip>
      <mu-raised-button @click="uploadPost" style="margin-top: 60px;" label="发表" fullWidth primary
                        :disabled="buttonEnable"/>

    </mu-content-block>


    <mu-dialog :open="dialog" title="警告" @close="dialog  = false">
      超出限制！
      <mu-flat-button slot="actions" primary @click="dialog  = false" label="确定"/>
    </mu-dialog>

  </div>
</template>

<script>
  export default {
    name: 'postedit',
    data () {
      return {
        post: {
          title: '',
          content: ''
        },
        titleErrorText: '',
        tagQA: false,
        tagShare: false,
        tagOther: true,
        imgList: [],
        dialog: false
      }
    },
    methods: {
      deleteImg (i) {
         //console.log(i);
        this.imgList.splice(i,1);
      },
      handleImg () {
          if(this.imgCount<3){
            let reader = new FileReader();
            reader.readAsDataURL(document.querySelector("input[type=file]").files[0]);
            reader.onloadend = result => {
              this.imgList.push( result.target.result)
            }
          }
          else
              this.dialog = true;

      },
      selectTagQA () {
        this.tagQA = !this.tagQA
        this.tagShare = false
        this.tagOther = false

       // console.log(this.tagQA, this.tagShare, this.tagOther)
      },
      selectTagShare () {
        this.tagShare = !this.tagShare
        this.tagQA = false
        this.tagOther = false

       // console.log(this.tagQA, this.tagShare, this.tagOther)
      },
      selectTagOther () {
        this.tagOther = !this.tagOther
        this.tagQA = false
        this.tagShare = false
       // console.log(this.tagQA, this.tagShare, this.tagOther)
      },
      handleTitleOverflow (isOverflow) {
        this.titleErrorText = isOverflow ? '超过限定长度' : ''
      },
      uploadPost () {
        if (this.tagQA) {
          this.$store.dispatch('uploadPost', {...this.post, tag: '问答',img:this.imgList})
        }
        else if (this.tagShare) {
          this.$store.dispatch('uploadPost', {...this.post, tag: '分享',img:this.imgList})
        }
        else if (this.tagOther) {
          this.$store.dispatch('uploadPost', {...this.post, tag: '其他',img:this.imgList})
        }else {
            window.alert('标签不能为空')
          return
        }
        this.$router.push('/')
      }
    },
    computed: {
      buttonEnable () {
        if (this.post.title != '' && this.post.content != '') {
          if (this.titleErrorText == '') {
            return false
          } else {
            return true
          }
        } else {
          return true
        }
      },
      imgCount () {
          return this.imgList.length;
      }
    },
  }
</script>

<style scoped>
  .chip {
    margin: 4px;
  }
</style>



